import React from "react";
import classNames from "classnames";
import { SectionSplitProps } from "../../utils/SectionProps";
import SectionHeader from "./partials/SectionHeader";
import Image from "../elements/Image";

const propTypes = {
  ...SectionSplitProps.types,
};

const defaultProps = {
  ...SectionSplitProps.defaults,
};

const FeaturesSplit = ({
  className,
  topOuterDivider,
  bottomOuterDivider,
  topDivider,
  bottomDivider,
  hasBgColor,
  invertColor,
  invertMobile,
  invertDesktop,
  alignTop,
  imageFill,
  ...props
}) => {
  const outerClasses = classNames(
    "features-split section",
    topOuterDivider && "has-top-divider",
    bottomOuterDivider && "has-bottom-divider",
    hasBgColor && "has-bg-color",
    invertColor && "invert-color",
    className
  );

  const innerClasses = classNames(
    "features-split-inner section-inner",
    topDivider && "has-top-divider",
    bottomDivider && "has-bottom-divider"
  );

  const splitClasses = classNames(
    "split-wrap",
    invertMobile && "invert-mobile",
    invertDesktop && "invert-desktop",
    alignTop && "align-top"
  );

  const sectionHeader = {
    title: "Workflow that just works",
    paragraph:
      "Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum — semper quis lectus nulla at volutpat diam ut venenatis.",
  };

  return (
    <section {...props} className={outerClasses}>
      <div className="container">
        <div className={innerClasses}>
          <SectionHeader data={sectionHeader} className="center-content" />
          <div className={splitClasses}>
            {/* /////////////////////////////////////////////////////////////////SECONDO ITEM////////////////////////////////// */}
            <div className="split-item">
              <div
                className="split-item-content center-content-mobile reveal-from-left"
                data-reveal-container=".split-item"
              >
                <div className="text-xxs text-color-primary fw-600 tt-u mb-8">
                  Microsoft programming language
                </div>
                <h3 className="mt-0 mb-12">Microsoft .Net C#</h3>
                <p className="m-0">
                  C# is a modern, object-oriented, type-safe programming
                  language. C# allows developers to build many types of secure
                  and reliable applications that run on .NET. C# has its roots
                  in the C family of languages ​​and will be instantly familiar
                  to programmers of C, C++, Java, and JavaScript.
                </p>
              </div>
              <div
                className={classNames(
                  "split-item-image center-content-mobile reveal-from-bottom",
                  imageFill && "split-item-image-fill"
                )}
                data-reveal-container=".split-item"
              >
                <a href="https://learn.microsoft.com/it-it/certifications/exams/az-104/">
                  <Image
                    src={require("./../../assets/images/c.png")}
                    alt="Features split 01"
                    width={528}
                    height={396}
                  />
                </a>
              </div>
            </div>
            {/* /////////////////////////////////////////////////////////////////PRIMO ITEM////////////////////////////////// */}
            <div className="split-item">
              <div
                className="split-item-content center-content-mobile reveal-from-left"
                data-reveal-container=".split-item"
              >
                <div className="text-xxs text-color-primary fw-600 tt-u mb-8">
                  Microsoft Azure
                </div>
                <h3 className="mt-0 mb-12">
                  Microsoft Associate Administrator
                </h3>
                <p className="m-0">
                  Implementing, managing, and monitoring an organization's
                  Microsoft Azure environment, including virtual networks,
                  storage, compute, identity, security, and governance. An Azure
                  administrator is often part of a larger team dedicated to
                  implementing an organization's cloud infrastructure. Azure
                  administrators also coordinate with other roles to deploy
                  Azure networking, security, database, application development,
                  and DevOps solutions.
                </p>
              </div>
              <div
                className={classNames(
                  "split-item-image center-content-mobile reveal-from-bottom",
                  imageFill && "split-item-image-fill"
                )}
                data-reveal-container=".split-item"
              >
                <a href="https://learn.microsoft.com/it-it/certifications/exams/az-104/">
                  <Image
                    src={require("./../../assets/images/cert-admi.jpg")}
                    alt="Features split 01"
                    width={528}
                    height={396}
                  />
                </a>
              </div>
            </div>
            {/* /////////////////////////////////////////////////////////////////TERZO ITEM////////////////////////////////// */}
            <div className="split-item">
              <div
                className="split-item-content center-content-mobile reveal-from-right"
                data-reveal-container=".split-item"
              >
                <div className="text-xxs text-color-primary fw-600 tt-u mb-8">
                  Microsoft Azure
                </div>
                <h3 className="mt-0 mb-12">
                  Microsoft Azure Security and Compliance
                </h3>
                <p className="m-0">
                  This is Microsoft's security, compliance and identity
                  management solutions can span these areas to offer a holistic
                  and comprehensive solution, which may include business
                  stakeholders, new or existing IT professionals, or students
                  interested in Microsoft solutions for security, compliance and
                  identity management.
                </p>
              </div>
              <div
                className={classNames(
                  "split-item-image center-content-mobile reveal-from-bottom",
                  imageFill && "split-item-image-fill"
                )}
                data-reveal-container=".split-item"
              >
                <Image
                  src={require("./../../assets/images/sec.jpg")}
                  alt="Features split 02"
                  width={528}
                  height={396}
                />
              </div>
            </div>
            {/* /////////////////////////////////////////////////////////////////QUARTO ITEM////////////////////////////////// */}
            <div className="split-item">
              <div
                className="split-item-content center-content-mobile reveal-from-left"
                data-reveal-container=".split-item"
              >
                <div className="text-xxs text-color-primary fw-600 tt-u mb-8">
                  Microsoft Azure
                </div>
                <h3 className="mt-0 mb-12">Microsoft Azure AI</h3>
                <p className="m-0">
                  With this certification, you gain knowledge of the concepts of
                  Machine Learning (ML) and Artificial Intelligence (AI) and
                  related Microsoft Azure services. With both technical and
                  non-technical backgrounds
                </p>
              </div>
              <div
                className={classNames(
                  "split-item-image center-content-mobile reveal-from-bottom",
                  imageFill && "split-item-image-fill"
                )}
                data-reveal-container=".split-item"
              >
                <Image
                  src={require("./../../assets/images/ai.jpg")}
                  alt="Features split 03"
                  width={528}
                  height={396}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

FeaturesSplit.propTypes = propTypes;
FeaturesSplit.defaultProps = defaultProps;

export default FeaturesSplit;
